.navigation {
    display: grid;
    grid-template-columns: 1fr 5fr 1fr;
    height: 5rem;
    background: $color-black;
    &__header {
        display: flex;
        justify-content: center;
        align-items: center; }
    &__nav {
        display: flex;
        justify-content: center;
        align-items: center; }
    &__list {
        display: flex;
        justify-content: center; }
    &__item {
        list-style-type: none;
        &:not(:last-child) {
            margin-right: 5.4rem; }
        &:hover {
            box-shadow: 0px 0px 18px 8px $color-purl;
            transform: scale(1.2);
            transition: 0.3s all; } }
    &__link {
        text-decoration: none;
        font-family: Montserrat;
        color: $color-grey;
        text-transform: uppercase;
        font-weight: 600;
        font-size: $font-size-text; }
    &__button {
        display: flex;
        justify-content: center;
        align-items: center; } }

.header-logo {
    text-decoration: none;
    font-family: Anton sans-serif;
    font-size: 2.8rem;
    background-color: $color-grey;
    letter-spacing: 0.42px;
    text-transform: uppercase;
    background-image: linear-gradient(125deg, rgba(163,74,255,1) 0%, rgba(71,11,71,1) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }

