.btn {
    font-size: $font-size-text;
    font-family: Montserrat;
    font-weight: 600;
    text-transform: uppercase;
    border: 0.1rem $color-purl solid;
    text-decoration: none;
    color: $color-purl;
    padding: 1.2rem 1.6rem;
    border-radius: 0.5rem;
    &:hover {
        box-shadow: 0px 0px 18px 8px $color-purl;
        transform: scale(1.2);
        transition: 0.3s all; }
    &__purl {
        background: $color-purl;
        color: $color-white; } }
