.section-about {
    position: relative;
    height: 100vh;
    background: $color-black;
    &__img {
        position: absolute;
        width: 30rem;
        height: 40rem;
        background:  linear-gradient(0deg, rgba($color-grey, 0.1) 0.10%, rgba($color-black, 0.1) 99.94%), url(../img/Lolly.JPG) center/contain no-repeat;
        top: 2rem;
        left: 40rem; }
    &__left {
        width: 40rem;
        height: 40rem;
        position: absolute;
        top: 10rem;
        left: 5rem; } }

.title {
    &__text {
        color: $color-white;
        font-size: 7rem;
        font-family: Anton;
        letter-spacing: .19rem;
        text-transform: uppercase;
        margin-bottom: 1.2rem; }
    &__text-one {
        color: $color-white;
        font-size: 2.4rem;
        font-family: Montserrat;
        font-weight: 700;
        line-height: 3rem;
        margin-bottom: 1.2rem; } }

.media {
    display: flex;
    position: absolute;
    top: 25rem;
    left: 25rem;
    &__link {
        &:not(:last-child) {
            margin-right: 5rem; }
        box-shadow: 0px 0px 10px 1px $color-purl;
        &:hover {
            box-shadow: 0px 0px 18px 8px $color-purl;
            transform: scale(1.2);
            transition: 0.3s all; } }
    &__img {
        width: 1.5rem;
        height: 1.5rem; } }


